#helpbar {
  width: 100%;
  height: 25px;
  background: #777;
  border-bottom: 1px solid #e6e6e6;
}

#helpbar .nav-content .nav-link {
  padding: 1px 10px;
  font-size: 12px;
  padding-bottom: 3px;
  display: inline-block;
  color: #e6e6e6;
  font-weight: bold;
}

#helpbar .nav-content .nav-link:hover {
  cursor: pointer;
  text-decoration: underline;
}

#helpbar .nav-content .nav-link.active {
  background: #fff;
  color: #777;
  border-radius: 5px 5px 0px 0px;
}

#header {
  border-bottom: 1px solid #e6e6e6;
}

#header .header-link {
  display: inline-block;
  color: #777;
  padding: 0px 8px;
  border-right: 1px solid #d6d6d6;
  transition: .25s;
}

#header .header-link:hover {
  cursor: pointer;
  color: #777;
  transform: scale(1.01);
}

#navbar {
  border-bottom: 1px solid #d6d6d6;
}

#navbar .access-content .access-link {
  text-transform: uppercase;
  display: block;
  float: left;
  font-size: 16px;
  color: #777;
  padding: 10px 20px;
  border-right: 1px solid #d6d6d6;
  transition: .25s;
}

#navbar .access-content .access-link:hover {
  cursor: pointer;
  color: #777;
  font-weight: bold;
}

#navbar .access-content .access-link.active {
  border-bottom: 3px solid #777;
  color: #777;
  font-weight: bold;
}

#footer {
  width: 100%;
  border-top: 1px solid #e6e6e6;
}

#footer .footer-item {
  color: #777;
}

#footer .footer-item .footer-item-title {
  position: relative;
  height: 24px;
  margin-bottom: 10px;
}

#footer .footer-item .footer-item-title span {
  position: absolute;
  left: 0px;
  background: #fff;
  padding-right: 10px;
}

#footer .footer-item .footer-item-title:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  top: 12px;
  background: #d6d6d6;
}

#footer .footer-item .footer-item-link {
  padding-left: 15px;
  margin-top: 10px;
  transition: .25s;
}

#footer .footer-item .footer-item-link:hover {
  cursor: pointer;
  color: #777;
  transform: scale(1.01);
}

#footer .footer-rights {
  border-top: 1px solid #e6e6e6;
  color: #777;
}

#sidebar {
  background: #fff;
  border-radius: 5px;
  padding: 5px;
}

#sidebar .group-divider {
  position: relative;
  color: #999;
  height: 24px;
  margin-top: 25px;
  margin-bottom: 10px;
}

#sidebar .group-divider span {
  background: #fff;
  padding-right: 10px;
  display: inline-block;
  position: absolute;
  left: 0px;
}

#sidebar .group-divider:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  left: 0px;
  top: 12px;
  background: #d6d6d6;
}

#sidebar .sidebar-item {
  color: #999;
  padding-left: 20px;
  margin-top: 5px;
  transition: .25s;
}

#sidebar .sidebar-item:hover {
  cursor: pointer;
  color: #777;
  transform: scale(1.01);
}

#sidebar .sidebar-item.active {
  color: #777;
  transform: scale(1.01);
}

#midiaModal .scroll-content {
  height: 200px;
  overflow-x: hidden;
  overflow-y: scroll;
}

.midia-content {
  background: #e6e6e6;
  overflow: hidden;
  border-radius: 5px;
  position: relative;
  width: 100px;
  height: 100px;
}

.midia-content img {
  transition: .25s;
}

.midia-content .btn-danger, .midia-content .btn-info {
  bottom: 5px;
  right: 5px;
  display: none;
}

.midia-content:hover {
  cursor: pointer;
}

.midia-content:hover img {
  opacity: 0.5;
}

.midia-content:hover .btn-danger, .midia-content:hover .btn-info {
  display: block;
}
